.site-menu {
  &__item {
    position: relative;
    list-style-type: none;
    height: 66px;
    display: flex;
    justify-content: center;
    align-items: center;
    box-shadow: inset 0 1px 0 @basic-grey-4;

    @media(min-width: @tablet-width) {
      box-shadow: none;

      &--current {
        &::after {
          content: "";
          position: absolute;
          bottom: 11px;
          left: 0;
          width: 100%;
          height: 2px;
          background-color: @basic-green;
        }
      }
    }
  }

  &__link {
    .font(20px, 20px, @basic-black, "Oswald", 500);
    text-transform: uppercase;
    text-decoration: none;

    @media(min-width: @tablet-width) {
      .font(18px, 24px);
    }
    @media (min-width: @desktop-width) {
      .font(20px, 30px);
    }
  }

  @media(max-width: @mobile-width-only) {
    &--closed {
      display: none;
    }
    &--opened {
      display: initial;
    }
  }
}
