.page-header {
  &__nav {
    display: flex;
    flex-direction: column;

    @media (min-width: @tablet-width) {
      flex-direction: row;
    }
  }
  &__menu {
    margin: 0;
    padding: 0;
    display: flex;
    flex-direction: column;
    background-color: @basic-white;
    position: absolute;
    width: 100%;
    left: 0;
    top: 65px;
    z-index: 2;

    @media (min-width: @tablet-width) {
      position: initial;
      flex-direction: row;
      gap: 36px;
    }
  }
  &__logo {
    text-decoration: none;
    display: flex;
    gap: 57px;
    align-items: center;

    @media (min-width: @tablet-width) {
      gap: 12px;
    }
  }
  &__toggle {
    position: absolute;
    top: 24px;
    right: 20px;
    z-index: 2;
    width: 24px;
    height: 16px;
    display: block;
    border: none;
    cursor: pointer;
    background-color: @basic-white;
    padding: 0;

    &::before {
      content: "";
      display: block;
      width: 24px;
      height: 2px;
      background-color: @basic-black;
      box-shadow: 
      0 -8px 0 0 @basic-black,
      0 8px 0 0 @basic-black;
    }
    @media (min-width: @tablet-width) {
      display: none;
    }
  }
}
